import React, {useContext} from 'react';
import DataDetailCom from "./DataDetailCom";
import {Context} from "../../Contex";

const DataCom = () => {
    const {array} = useContext(Context);
    console.log(array)
    return (
        <div>
            {
                 array.map(el => {
                     return <DataDetailCom el={el}/>
                })
            }
        </div>
    );
};

export default DataCom;